import React from "react";
import { useEffect, useState } from "react";
import { TouchableOpacity, View } from "react-native";

import { Excursion } from "../components/Excursion.js";
import AsyncStorage from "@react-native-async-storage/async-storage";


import dataProducts from "../data/dataProducts.json";
import { URLRequest, token } from "../data/config";

export default function Main({ navigation }) {
  const [excursions, setExcursions] = useState([]);

  async function respondGetExcursions() {
    try {
      fetch(`${URLRequest}/excursion/`, {
        mode: "cors",
        method: "GET",
        headers: {
          "ngrok-skip-browser-warning":
            "Mozilla/5.0 (iPhone; CPU iPhone OS 16_6 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/16.6 Mobile/15E148 Safari/604.1",
        },
      })
        .then((response) => response.json())
        .then((data) => setExcursions(data.excursions))
        .catch((error) => console.error("Ошибка:", error));
    } catch (error) {
      console.error("Error fetching excursions: ", error);
    }
  }
  const _setItem = async () => {
    try {
      await AsyncStorage.setItem("UserId", await userIdFunc());
    } catch (error) {
      // Error saving data
    }
  };
  const userIdFunc = async () => {
    let user = ""
    if (window.Telegram?.WebApp) {
      const tg = window.Telegram.WebApp;
      user = tg.initDataUnsafe?.user;
    } else {
      return `User ID: Ne rabotaet blyat'`;
    }
    if (user) {
      return `User ID: ${user}`;
    }
  };
  useEffect(() => {
    const fetchData = async () => {
      await respondGetExcursions();
      // <script src="https://telegram.org/js/telegram-web-app.js"></script>
      console.log(excursions);
      await _setItem();
    };
    fetchData();

    // setExcursions(dataProducts["excursions"]);
  }, []);

  if (!excursions || excursions.length === 0) {
    return null;
  }

  return (
    <View>
      {excursions.map((excursion) => {
        return (
          <TouchableOpacity
            key={excursion["id"]}
            onPress={() => navigation.navigate("ProductPage", excursion)}
          >
            <Excursion object={excursion}></Excursion>
          </TouchableOpacity>
        );
      })}
    </View>
  );
}
