import React, { useEffect, useState } from "react";
import { URLRequest, botName } from "../../data/config";
import { Linking } from "react-native";
import AsyncStorage from "@react-native-async-storage/async-storage";

export default function Pay( excursion ) {
  console.log(excursion.id);
  const _retrieveData = async () => {
    try {
      const value = await AsyncStorage.getItem("UserId");
      if (value !== null) {
        return value;
      }
    } catch (error) {}
  };
  const link = async (UserId) => {
    fetch(`${URLRequest}/excursion/successful_payment`, {
      mode: "cors",
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      body: JSON.stringify({
        username: await _retrieveData(),
        excur_id: excursion.excur_id,
      }),
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const contentType = response.headers.get("content-type");
        if (contentType && contentType.includes("application/json")) {
          return response.json();
        } else {
          throw new Error("Response is not JSON");
        }
      })
      .then((data) =>
        Linking.openURL(data.url_bot).catch((err) =>
          console.error("An error occurred", err)
        )
      )
      .catch((error) => console.error("Ошибка:", error));
  };
  // useEffect(() => {
    link( _retrieveData());
  // }, []);
}
