import React from "react";
import { useEffect, useState } from "react";
import { View } from "react-native";

import { ButtonPay } from "../components/ButtonPay.js";
import { TextBlock } from "../components/TextBlock.js";
import { ImageProduct } from "../components/ImageProduct.js";

// import { URLRequest, token } from "../data/config";

export default function ProductPage({ navigation, route }) {
  const [excursion, setExcursion] = useState({});

  async function GetExcursion() {
    setExcursion(route.params);
  }

  useEffect(() => {
    GetExcursion();
  }, []);

  return (
    <View>
      <ImageProduct photo={excursion.photo} />
      <TextBlock
        Name={excursion.name}
        Price={excursion.price}
        Desc={excursion.desc}
      />
      <ButtonPay excursion={excursion} />
    </View>
  );
}
